<template>
  <div class="jb-check-radio-switch-group" :class="{'is-grouped-column':column}">
    <label v-for="(value, key) in options" :key="key" :class="type" class="jb-label-wrap">
      <input
        :type="inputType"
        :name="name"
        v-model="computedValue"
        :value="key">
      <span class="check"></span>
      <span class="control-label">{{ value }}</span>
    </label>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'CheckRadioPicker',
  props: {
    options: {
      type: Object,
      default: () => {}
    },
    name: {
      type: String,
      required: true
    },
    type: {
      type: String,
      default: 'checkbox'
    },
    column: Boolean,
    modelValue: [String, Number, Boolean, Array]
  },
  emits: ['update:modelValue'],
  setup (props, { emit }) {
    const computedValue = computed({
      get: () => props.modelValue,
      set: value => {
        emit('update:modelValue', value)
      }
    })

    const inputType = computed(() => props.type === 'radio' ? 'radio' : 'checkbox')

    return {
      computedValue,
      inputType
    }
  }
}
</script>
